"use client";

import Image from "next/image";
import { notFound } from "next/navigation";
import { Ebook, ebooks } from "@/lib/data";
import { PlaceHolderImages } from "@/lib/placeholder-images";
import { Button } from "@/components/ui/button";
import { useCart } from "@/context/cart-context";
import { Loader2, ShoppingCart } from "lucide-react";
import { EbookCard } from "@/components/ebook-card";

export default function EbookDetailPage({ params }: { params: { id: string } }) {
  const { addToCart } = useCart();
  
  const ebookId = parseInt(params.id, 10);
  const ebook = ebooks.find((e) => e.id === ebookId);

  if (!ebook) {
    // If ebook is not found, you can render a not found page
    // or handle it as you see fit.
    notFound();
    return null; // or a loading/error component
  }

  const image = PlaceHolderImages.find((img) => img.id === ebook.coverImageId);

  const relatedEbooks = ebooks
    .filter((e) => e.id !== ebook.id)
    .sort(() => 0.5 - Math.random()) // shuffle
    .slice(0, 4);

  return (
    <div className="container mx-auto px-4 py-8 md:py-12">
      <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
        <div className="flex justify-center">
          <div className="relative aspect-[2/3] w-full max-w-sm rounded-lg overflow-hidden shadow-lg">
            {image && (
              <Image
                src={image.imageUrl}
                alt={`Cover of ${ebook.title}`}
                data-ai-hint={image.imageHint}
                fill
                className="object-cover"
                sizes="(max-width: 640px) 100vw, 50vw"
                priority
              />
            )}
          </div>
        </div>
        <div className="flex flex-col justify-center">
          <h1 className="font-headline text-3xl md:text-4xl font-bold mb-2">
            {ebook.title}
          </h1>
          <p className="text-lg text-muted-foreground mb-4">
            by {ebook.author}
          </p>
          <p className="text-2xl font-bold text-primary mb-6">
            GH₵{ebook.price.toFixed(2)}
          </p>
          <p className="text-base text-foreground/80 leading-relaxed mb-6">
            {ebook.description}
          </p>
          <Button onClick={() => addToCart(ebook)} size="lg" className="w-full md:w-auto">
            <ShoppingCart className="mr-2" />
            Add to Cart
          </Button>
        </div>
      </div>
      
      <section className="py-12 md:py-20 mt-8 border-t">
        <h2 className="font-headline text-2xl md:text-3xl font-bold text-center mb-10">
          You Might Also Like
        </h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
          {relatedEbooks.map((relatedEbook) => (
            <EbookCard key={relatedEbook.id} ebook={relatedEbook} />
          ))}
        </div>
      </section>
    </div>
  );
}
